.u-color-inherit { color: inherit; }
.u-muted { opacity: 0.5; }

@add-mixin colors dark, var(--color-dark);
@add-mixin colors light, var(--color-light);
@add-mixin colors primary, var(--color-primary);
@add-mixin colors secondary, var(--color-secondary);
@add-mixin colors danger, var(--color-danger);
@add-mixin colors info, var(--color-info);
@add-mixin colors success, var(--color-success);
@add-mixin colors warning, var(--color-warning);
